import React, { useState } from "react"
import { Link, graphql } from "gatsby"
import { DiscussionEmbed, CommentCount } from 'disqus-react';
import styled from "styled-components";

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm, scale } from "../utils/typography"
import { SocialWithMessage } from "../components/social";

const MOBILE_WIDTH = 600;
const MOBILE_QUERY = `(max-width: ${MOBILE_WIDTH}px)`;

const Title = styled.h1`
  margin: ${rhythm(1)} 0 0;
  @media ${MOBILE_QUERY} {
    font-size: 36px;
  }
`;

const CommentsWrapper = styled.div`
  margin-top: 24px;
  min-height: 130px;
`;

const ShowCommentsBtn = styled.div`
  display: inline-block;
  cursor: pointer;
  padding: 8px 16px;
  font-size: 14px;
  border: 2px solid currentColor;
  border-radius: 4px;
`;

const messages = {
  en: {
    alsoAvailableInDiffLang: 'Эта статья также доступна на русском',
  },
  ru: {
    alsoAvailableInDiffLang: 'This post is also available in English',
  }
}

const BlogPostTemplate = (props) => {
  const post = props.data.markdownRemark
  const { previous, next, translatedPost } = props.pageContext
  const { langKey, disqusIdOverride } = post.frontmatter;
  const fullUrl = `https://sergey.blog/${post.frontmatter.url}`;


  const [isCommentsVisible, setIsCommentsVisible] = useState(false);
  const disqusShortname = langKey === 'ru' ? 'sergey-blog-ru' : 'sergey-blog';
  const disqusConfig = {
    url: fullUrl,
    identifier: disqusIdOverride || fullUrl,
    title: post.frontmatter.title,
  };

  return (
    <Layout location={props.location} langKey={langKey}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
        langKey={langKey}
      />
      <article>
        <header>
          <Title>
            {post.frontmatter.title}
          </Title>

          {
            translatedPost &&
            <Link
              to={translatedPost.node.frontmatter.url}
              style={{ display: 'inline-block', marginTop: rhythm(0.2) }}
            >
              {messages[langKey].alsoAvailableInDiffLang}
            </Link>
          }

          <p
            style={{
              display: `block`,
              marginTop: rhythm(0.6),
              marginBottom: rhythm(1),
            }}
          >
            {post.frontmatter.date}
          </p>
        </header>
        <section dangerouslySetInnerHTML={{ __html: post.html }} />
      </article>


      <hr style={{ margin: `${rhythm(2)} 0` }} />

      <SocialWithMessage langKey={langKey} />

      {/* <footer>
        <Bio langKey={langKey} />
      </footer> */}

      {
        (previous || next) && (
          <nav style={{ marginBottom: rhythm(2) }}>
            <ul
              style={{
                display: `flex`,
                flexWrap: `wrap`,
                justifyContent: `space-between`,
                listStyle: `none`,
                padding: 0,
              }}
            >
              <li>
                {previous && (
                  <Link to={previous.node.frontmatter.url} rel="prev">
                    ← {previous.node.frontmatter.title}
                  </Link>
                )}
              </li>
              <li>
                {next && (
                  <Link to={next.node.frontmatter.url} rel="next">
                    {next.node.frontmatter.title} →
                  </Link>
                )}
              </li>
            </ul>
          </nav>
        )
      }

      <CommentsWrapper>
        {
          isCommentsVisible
          ? <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
          : <ShowCommentsBtn onClick={() => setIsCommentsVisible(true)}>
              {langKey === 'ru' ? 'Показать комментарии' : 'Show comments'}
            </ShowCommentsBtn>
        }
      </CommentsWrapper>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        langKey
        url
        disqusIdOverride
      }
    }
  }
`
